}

button {
    padding: 10 20;
    background-color: #0B5671;
    outline: none;
    border: none;
    cursor: pointer;
    color: #E5E5E5;
    text-transform: uppercase;
    font-weight: 400;
    border-radius: 5px;
    width: fit-content;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  &:hover {
    background-color: #B9D7E1;
    color: #0B5671;
    font-weight: bold;
  }
}
